<template>
  <div class="basic_modeling">
    <div class="basic_top" v-if="basicData.imgUrl">
      <img :src="require(`../../assets/${basicData.imgUrl}`)">
      <div class="basic_text">
        <p>{{basicData.index}}</p>
        <p>{{basicData.name}}</p>
        <p>{{basicData.introduce}}</p>
      </div>
    </div>
    <div class="basic_bottom" v-if="mainData.length !== 0">
      <div class="project_top">
        <h1>项目展示</h1>
        <div class="project_main">
          <div class="project_left project_fitst">
            <img :src="require(`../../assets/${mainData[0].imgUrl}`)" @click="detailsClick(mainData[0])" alt="">
            <h2 @click="detailsClick(mainData[0])">{{mainData[0].name}}</h2>
            <p>{{mainData[0].describe}}</p>
          </div>
          <div class="project_left">
            <img :src="require(`../../assets/${mainData[1].imgUrl}`)" @click="detailsClick(mainData[1])" alt="">
            <h2 @click="detailsClick(mainData[1])">{{mainData[1].name}}</h2>
            <p>{{mainData[1].describe}}</p>
          </div>
        </div>
      </div>
      <div class="project_bottom">
        <h1>项目展示</h1>
        <div class="project_time">
          <p>2020</p>
          <p>2021</p>
          <p>2022</p>
        </div>
        <div class="secondary">
          <div class="secondary_main" v-for="(item, index) in project" :key="index">
            <div>
              <img :src="require(`../../assets/${item.imgUrl}`)" @click="detailsClick(item)">
              <div class="secondary_name">{{item.name}}</div>
              <div class="secondary_button" @click="detailsClick(item)">详情&按钮</div>
            </div>
          </div>
        </div>
        <div class="switch">
          <i class="el-icon-back" @click="leftClick"></i>
          <i class="el-icon-right" @click="rightClick"></i>
        </div>
      </div>
    </div>
    <Footers></Footers>
    <el-dialog :visible.sync="dialogVisible" :modal-append-to-body="false" append-to-body :before-close="handleClose">
      <div class="details_left" v-if="details.name">
        <video id="video" width="100%" controls loop autoplay="autoplay" v-if="details.videoUrl !== ''">
          <source :src="require(`../../assets/${details.videoUrl}`)" type="video/mp4">
        </video>
        <div v-for="(item,index) in details.imgUrl" :key="index">
          <img :src="require(`../../assets/${item}`)">
        </div>
      </div>
      <div class="details_right">
        <div class="details_name">
          <h1>{{details.name}}</h1>
          <p>使用软件与技术：{{details.techniques}}</p>
        </div>
        <div class="details_introduce">
          <h1>项目介绍</h1>
          <p>{{details.introduce}}</p>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Footers from './Footers.vue'
export default {
  name: '',
  components: {
    Footers
  },
  watch: {
    HandlerName: {
      deep: true, // 深度监听
      handler (newVal, oldVal) {
        if(newVal !== oldVal){
          this.HandlerName = newVal
          this.initialization()
        }
      }
    }
  },
  props: {
    HandlerName: String
  },
  data () {
    return {
      "projectData":[],
      "detailsData": [],
      "mainData": [],
      "basicData":{},
      projectIndex: 1,
      details:{},
      project:[],
      dialogVisible: false,
      tabadata:null
    }
  },
  computed: {},
  created() {},
  mounted () {
    this.initialization()
  },
  methods: {
    // 页面初始化
    initialization() {
      this.$axios.get('Personal.json').then((res) => {
        console.log(this.HandlerName,'====');
        let item = null
        if(this.HandlerName === '基础建模') {
          item = res.data.BasicModeling
        }
        if(this.HandlerName === '人物形象IP设计') {
          item = res.data.CharacterImage
        }
        if(this.HandlerName === '动效设计') {
          item = res.data.DynamicDesign
        }
        if(this.HandlerName === '环境设计') {
          item = res.data.Environmental
        }
        this.projectData = item.projectData
        this.detailsData = item.detailsData
        this.mainData = item.mainData
        this.basicData = item.basicData
        this.$nextTick(() => {
          if(this.projectData.length !== 0) {
            this.project =  this.projectData.slice((this.projectIndex - 1) * 6, this.projectIndex * 6)
            document.querySelector('.el-icon-back').style.cursor = 'not-allowed'
            if (Math.ceil(this.projectData.length/6) == this.projectIndex) {
              document.querySelector('.el-icon-right').style.cursor = 'not-allowed'
            }
          }
        })
      })
    },
    // 详情页弹框
    detailsClick (data) {
      this.dialogVisible = true
      this.details = {}
      this.detailsData.map(item => {
        if(item.name === data.name) {
          this.details = item
        }
      })
      if (this.details.videoUrl) {
        this.$nextTick(() => {
          document.getElementById("video").load()
        })
      }
    },
    // 详情页关闭
    handleClose(done) {
      if (this.details.videoUrl) {
        document.getElementById('video').pause()
      }
      done()
    },
    // 项目展示右切换。
    rightClick() {
      if (Math.ceil(this.projectData.length/6) > this.projectIndex) {
        console.log('====', this.projectIndex, this.projectData.length/6);
        this.projectIndex = this.projectIndex + 1
        this.project =  this.projectData.slice((this.projectIndex - 1) * 6, this.projectIndex * 6)
        document.querySelector('.el-icon-back').style.cursor = 'pointer'
      }
      if (Math.ceil(this.projectData.length/6) == this.projectIndex) {
        document.querySelector('.el-icon-right').style.cursor = 'not-allowed'
      }
    },
    // 项目展示左切换.
    leftClick() {
      if (Math.ceil(this.projectData.length/6) <= this.projectIndex) {
        this.projectIndex = this.projectIndex - 1
        this.project =  this.projectData.slice((this.projectIndex - 1) * 6, this.projectIndex * 6)
        document.querySelector('.el-icon-right').style.cursor = 'pointer'
      }
      if (this.projectIndex == 1)  {
        document.querySelector('.el-icon-back').style.cursor = 'not-allowed'
      }
    }
  }
}
</script>

