<template>
  <div class="Personal" id="topAnchor">
    <div class="center" :style="{transform:scalestyle}">
      <div v-if="isShow">
        <div class="Navigation">
          <div class="name">陈彤</div>
          <el-menu
            :default-active="activeIndex"
            class="el-menu-demo"
            mode="horizontal"
            @select="handleSelect"
            text-color="#fff"
            active-text-color="#ffd04b">
            <el-menu-item index="3D设计">3D设计</el-menu-item>
            <el-menu-item index="平面设计">平面设计</el-menu-item>
            <el-menu-item index="画册设计">画册设计</el-menu-item>
            <el-menu-item index="品牌设计">品牌设计</el-menu-item>
            <el-menu-item index="电商设计">电商设计</el-menu-item>
          </el-menu>
        </div>
        <D v-if="activeIndex==='3D设计'"></D>
        <Brand v-if="activeIndex==='品牌设计'"></Brand>
        <Brochure v-if="activeIndex==='画册设计'"></Brochure>
        <ECommerce v-if="activeIndex==='电商设计'"></ECommerce>
        <Graphic v-if="activeIndex==='平面设计'"></Graphic>
      </div>.
      <Cover v-if="!isShow"></Cover>
    </div>..
    <!-- <a id="TOPUp" @click="toTop" href="Personal"> -->
    <a id="TOPUp" @click="toTop">
      <i class="el-icon-arrow-up"></i>
    </a>
  </div>
</template>

<script>
import D from '../components/Personal/3D.vue'
import Brand from '../components/Personal/Brand.vue'
import Brochure from '../components/Personal/Brochure.vue'
import ECommerce from '../components/Personal/ECommerce.vue'
import Graphic from '../components/Personal/Graphic.vue'
import Cover from '../components/Personal/Cover.vue'
export default {
  name: '',
  components: {
    D,
    Brand,
    Brochure,
    ECommerce,
    Graphic,
    Cover
  },
  watch: {},
  props: {},
  data () {
    return {
      scalestyle: '',
      activeIndex: '3D设计',
      isShow: true
    }
  },
  computed: {},
  mounted () {
    this.changesize()
    window.onresize = () => {
      if (this.timerHander) {
        window.clearTimeout(this.timerHander)
      }
      this.timerHander = setTimeout(() => {
        this.changesize()
      }, 50)
    }
    window.addEventListener('scroll', this.scrollToTop)
  },
  destroyed () {
    window.removeEventListener('scroll', this.scrollToTop)
  },
  methods: {
    changesize () {
      const rwidth = parseFloat(window.innerWidth)
      const rheight = parseFloat(window.innerHeight)
      const scalew = rwidth / 1920
      const scaleh = rheight / 1080
      this.scalestyle = 'scale(' + scalew + ', ' + scaleh + ')'
    },
    handleSelect(key) {
      this.activeIndex = key
    },
    // 回到顶部
    toTop () {
      const that = this
      let timer = setInterval(() => {
        let ispeed = Math.floor(-that.scrollTop / 5)
        document.documentElement.scrollTop = document.body.scrollTop = that.scrollTop + ispeed
        if (that.scrollTop === 0) {
          clearInterval(timer)
        }
      }, 20)
    },
    scrollToTop () {
      const that = this
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      that.scrollTop = scrollTop
    }
  }
}
</script>
