<template>
  <div>
    <div class="D">
      <div class="Handler" @click="clickHandler">
        <div class="Part"><p>Part 1</p>基础建模</div>
        <div><p>Part 2</p>人物形象IP设计</div>
        <div><p>Part 3</p>动效设计</div>
        <div><p>Part 4</p>环境设计</div>
      </div>
    </div>
    <BasicModeling :HandlerName="HandlerName"></BasicModeling>
  </div>
</template>

<script>
import BasicModeling from './BasicModeling'

export default {
  name: '',
  components: {
    BasicModeling,
  },
  watch: {},
  props: {},
  data () {
    return {
      HandlerName: '基础建模',
    }
  },
  computed: {},
  mounted () {
    document.querySelector(".Handler").children[0].classList.add("Part");
  },
  methods: {
    clickHandler(e) {
      this.HandlerName = e.target.innerText.slice(8,e.target.innerText.length)
      document.querySelector(".Handler").children[0].classList.remove("Part");
      document.querySelector(".Handler").children[1].classList.remove("Part");
      document.querySelector(".Handler").children[2].classList.remove("Part");
      document.querySelector(".Handler").children[3].classList.remove("Part");
      if (this.HandlerName === '基础建模') {
        document.querySelector(".Handler").children[0].classList.add("Part");
      }
      if (this.HandlerName === '人物形象IP设计') {
        document.querySelector(".Handler").children[1].classList.add("Part");
      }
      if (this.HandlerName === '动效设计') {
        document.querySelector(".Handler").children[2].classList.add("Part");
      }
      if (this.HandlerName === '环境设计') {
        document.querySelector(".Handler").children[3].classList.add("Part");
      }
    },
  }
}
</script>

